body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

h1, h2, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav {
  margin-bottom: 50px;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.btn {
  width: 100px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: 10px 5px;
  font-family: Roboto Mono, monospace;
  font-size: 16px;
  font-weight: 500;
}

.nav__list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.nav__btn {
  color: #fff;
  background-color: #8dc6f2;
}

.todo__link {
  color: #8dc6f2;
  text-decoration: none;
}

.todo__title {
  text-align: center;
  margin-bottom: 20px;
  font-family: Dancing Script, cursive;
  font-size: 36px;
}

.todo__form {
  width: 50%;
  gap: 10px;
  margin: 0 auto 50px;
  display: flex;
}

.todo__field {
  width: calc(100% - 100px);
  color: #8dc6f2;
  border: 1px solid #8dc6f2;
  border-radius: 20px;
  padding: 15px;
  font-size: 18px;
}

.todo__field::-ms-input-placeholder {
  color: rgba(141, 198, 242, .616);
}

.todo__field::placeholder {
  color: rgba(141, 198, 242, .616);
}

.todo__field:focus {
  outline: none;
}

.todo__submit {
  text-transform: uppercase;
  color: #8dc6f2;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #8dc6f2;
  padding: 15px 10px;
  font-size: 18px;
  transition: all .2s cubic-bezier(.4, 0, .2, 1);
}

.todo__submit:hover {
  color: #fff;
  background-color: #8dc6f2;
}

.todo__list {
  width: 50%;
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
  display: flex;
}

.todo__item {
  border: 1px solid #8dc6f2;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.todo__item-done {
  border: 3px solid rgba(86, 179, 70, .627);
}

.todo__input {
  border: none;
  flex-grow: 1;
  font-family: Roboto, sans-serif;
  font-size: 18px;
}

.todo__input:focus {
  outline: none;
}

.todo__btn-item {
  width: 50px;
  text-transform: lowercase;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 10px;
  padding: 0 5px;
  font-family: Roboto Mono, monospace;
  font-size: 12px;
}

.todo__edit {
  color: rgba(212, 160, 92, .647);
}

.todo__done {
  color: rgba(86, 179, 70, .627);
}

.todo__delete {
  color: rgba(241, 30, 30, .663);
}

